 * {
   font-family: 'Roboto', sans-serif;
   font-size  : calc(9px + 0.9vw);
   line-height: calc(1.5em + 0.5vw);
   color: #494949;
  }

 .navbar {
   background-color: rgb(0, 0, 0);
 }

 .navs {
   margin: auto;
   color: whitesmoke;
 }
.section-title{
color: #bc0114;
margin: auto;
padding-top: 3vh;
padding-bottom: 3vh;
}

 /*landing section */
 .landing-space {
   background-image      : url(/src/assets/images/flippedheader.jpeg);
   background-color      : black;
   height                : 100vh;
   background-position   : center;
   background-repeat     : no-repeat;
   background-size       : cover;
   /* border-bottom      : black 10px solid; */
   border-left           : black 5px solid;
   border-right          : black 5px solid;
   position              : relative;

   text-shadow           : 1px 1px 4px#6d2518, 0px 0px 13px #e9d4d4, 0px 0px 8px #b32121;

 }

 .landing-text {
   text-align: center;
   position  : absolute;
   left      : 10%;
   top       : 45%;
   transform : translate(0, -50%);
 }

 #landing-hdr {
   font-weight: 700;
   font-size  : 4.5em;
 }

 #landing-intro {
   font-size: 2.5em;
 }

 /* dividers */
 .section-divider {
   background-color: rgb(0, 0, 0);
   height          : 10px;
 }

 /* about section */
 .about-row {
  color           : rgba(17, 17, 17, 0.822); 
  padding-bottom: 3vh;
 }
 .about-cntnr .section-title{
   margin-left: 55vw;
 }
 .thumbnail-pic {
   content      : url(/src/assets/images/profile.jpeg);
   max-width    : 27em;
   max-height   : 27em;
   height       : 20em;
   width        : 20em;
   border-radius: 15em;
   overflow     : auto;
   border: #bc0114d5 solid 5px;
  
 }

 .txt-div {
   width           : 60%;
   color           : rgba(8, 8, 8, 0.822);

   margin: auto;
 }

 .about-text-edu {
   font-weight: 900;
   margin     : auto;
   max-width  : fit-content;
   text-align : center;
   padding-top: 1.5vw;
 

 }

 
/*Skills Section*/
 .skills-row {
  background-color:  rgba(243, 243, 243, 0.527);
   width : 100%;
 display: flex;
 justify-content: center;
 align-items: center;
 padding-bottom: 3vw;
 
 }
.skills-deck{
  width: 60vw;
  text-align: center; 
}
.skills-group{
  padding-left: 0%;
  border-bottom: solid #bc0114d5 5px ;
}

.skills-hdr{
  font-weight: 600;
  width: 100%;
  height: fit-content;
  font-size: 1.5em;
}

 .skills-items {
   list-style-type: none;
   font-size      : 1.2em;
   padding: 0%;
 } 
 .skills-icon{
   padding: 2px;
 border-radius: 25px;
  background-color: #c50316ab;
  height: 5vh;
  width: 2.5vw;
 }
 



 /* projects section */
 .project-row {
   padding-bottom  : 5vh;
   padding-top  : 5vh;
 }

 .projects-carousel {
   width           : 40vw;
   border-bottom         : solid rgb(155, 29, 29) 4px;
   border-top       : solid rgb(155, 29, 29) 4px;
   background-color: rgba(248, 246, 246, 0.973);
   border-radius   : 0.5cm;
   padding         : 1.5vh;
   width           : 47vmin;
   height          : 60vmin;
 }

 .project-imgs {

   border-radius: 0.2cm;
   display      : block;
   margin-left  : auto;
   margin-right : auto;
   width        : 44.3vmin;
   height       : 38vmin;
 }

 .car-col {
   margin: auto;

 }

 .headers {
   max-width: fit-content;
   margin   : auto;
   padding  : 2vmin;
   color: #494949;
 }

 .card-txt {
   margin    : auto;
   text-align: center;
 }

 /*contact*/
 .contact-container {
   margin    : auto; 
 }

 .contact-img {
   width             :3.5em;
   background-color  : white;
   border            : solid rgba(230, 230, 230, 0.993) 1px;
   margin            : 5px;
 }

 /*footer*/
 .footer {
   background-color: #000000ec;
   color           : rgba(255, 255, 255, 0.993);
   height          : 12em;
   border-bottom: black solid 20px;


 }

 .footer p {
   margin     : auto;
   /* text-shadow: 0px 0px 10px #ffffff, 2px 2px 20px #969696; */
   max-width: fit-content;
   padding-top: 5vh;
 }


 /* ---------MOBILE ------------------*/

 @media screen and (max-width: 50em) {

   /*landing section */
   .landing-space {
     background-image: url(/src/assets/images/13.jpeg);
   }

   /*about section */
   .thumbnail-pic {
     content      : url(/src/assets/images/headshotsmobile.jpeg);
     margin-bottom: 10px;
   }

   .about-row {
     text-align      : center;
     background-color: black;
     color           : rgba(255, 255, 255, 0.822);
     padding         : 0px;
   }

   .txt-div {
     width: 100vw;
   }

   .skills-row {
     background-color: #91473C;
     width           : 100vw;
   }

   .skills-group {
     text-shadow: 1px 1px 4px#e69f9f, 0px 0px 13px #e9d4d4, 0px 0px 8px #332626;
   }

   .skills-items {
     display: inline;
     margin : auto;

   }

   /* projects section */
   .projects-carousel {
     width : 80vw;
     height: 67vh;
   }

   .project-imgs {
     width : 75vw;
     height: 50vh;
   }
 }

/* -------------Breaking Points in Between Mobile and Desktop------------ */

 @media only screen and (min-width: 50em) and (max-width:95.5em) {
 
  .about-row {
    text-align      : center;
    background-color: black;
    color           : rgba(255, 255, 255, 0.822);
  }

  

  .txt-div {
    /* border : chartreuse solid 2px; */
    padding         : 15px;
    width: 100vw;
    margin:auto;

  }

  .about-text-edu {
    /* font-weight: 900; */
    margin-top     : 12px;
    padding-right: 5px;


  }
  .skills-row {
    background-color: #bc0114ad;
    width           : 100vw;

  }

  .skills-cntnr{
 

    max-width: 90vw;
    margin-right: auto;
    margin-left: auto;

 

  }
  .skills-group {
      margin: auto;
      margin-right: auto;
    margin-left: auto;
    
  }

  .skills-items {
    display: inline;

  }

}

@media only screen and (min-width: 50em) and (max-width: 65em) {
  .projects-carousel {
    width : 70vw;
    height: 70vh;
  }

  .project-imgs {
    width : 65vw;
    height: 50vh;
  }
  
}
@media only screen and (min-width: 65em) and (max-width: 95.5em) {
  .projects-carousel {
    width : 40vw;
    height: 65vh;
  }

  .project-imgs {
    width : 35vw;
    height: 45vh;
  }
  
}

